import React, { useState, useEffect } from 'react';
import DeviceAdvancedMenu from './DeviceAdvancedMenu';
import { 
  FaDesktop, FaMobile, FaTabletAlt, FaUsers, FaPlug, FaLock, FaChartLine, 
  FaCog, FaPowerOff, FaSearch, FaFilter, FaEllipsisV, FaExclamationCircle, 
  FaCheckCircle, FaInfoCircle, FaDownload, FaCamera, FaMicrophone, FaVideo, FaKey, FaFile, FaUser, FaLockOpen 
} from 'react-icons/fa';
import { 
  LineChart, Line, BarChart, Bar, XAxis, YAxis, CartesianGrid, 
  Tooltip, Legend, ResponsiveContainer, PieChart, Pie, Cell 
} from 'recharts';

const ControlPanel = () => {
  //  
  const [devices, setDevices] = useState([
    {
      id: '1',
      name: ' Samsung Galaxy S21',
      type: 'mobile',
      status: 'online',
      battery: 85,
      lastSeen: ' 5 ',
      ip: '192.168.1.105',
      location: '',
      features: [' ', '', ' ']
    },
    {
      id: '2',
      name: ' iPhone 13',
      type: 'mobile',
      status: 'online',
      battery: 62,
      lastSeen: '',
      ip: '192.168.1.108',
      location: '',
      features: [' ', '']
    },
    {
      id: '3',
      name: ' Xiaomi Redmi',
      type: 'mobile',
      status: 'offline',
      battery: 45,
      lastSeen: ' ',
      ip: '192.168.1.112',
      location: '',
      features: ['']
    },
    {
      id: '4',
      name: '  iPad',
      type: 'tablet',
      status: 'online',
      battery: 78,
      lastSeen: ' ',
      ip: '192.168.1.115',
      location: '',
      features: [' ', '', ' ']
    }
  ]);

  //     
  const [openDeviceMenu, setOpenDeviceMenu] = useState(null);

  //  
  const [activities, setActivities] = useState([
    { id: '1', device: ' Samsung Galaxy S21', action: ' ', time: '10:30:25', status: 'success' },
    { id: '2', device: ' iPhone 13', action: ' ', time: '10:28:15', status: 'success' },
    { id: '3', device: '  iPad', action: ' ', time: '10:25:40', status: 'error' },
    { id: '4', device: ' Xiaomi Redmi', action: ' ', time: '10:20:10', status: 'warning' },
    { id: '5', device: ' Samsung Galaxy S21', action: ' ', time: '10:15:30', status: 'success' }
  ]);

  //  
  const [sessionStats, setSessionStats] = useState([
    { name: '', sessions: 12, duration: '3:45' },
    { name: '', sessions: 8, duration: '2:30' },
    { name: '', sessions: 15, duration: '4:15' },
    { name: '', sessions: 10, duration: '3:20' },
    { name: '', sessions: 6, duration: '1:50' },
    { name: '', sessions: 18, duration: '5:10' },
    { name: '', sessions: 14, duration: '4:05' }
  ]);

  //  
  const [deviceStats, setDeviceStats] = useState([
    { name: '', value: 65 },
    { name: ' ', value: 20 },
    { name: ' ', value: 15 }
  ]);

  //  
  const [statusStats, setStatusStats] = useState([
    { name: '', value: 75 },
    { name: ' ', value: 25 }
  ]);

  //       
  const toggleDeviceMenu = (deviceId) => {
    setOpenDeviceMenu(openDeviceMenu === deviceId ? null : deviceId);
  };

  //    
  const executeAdvancedCommand = (deviceId, command) => {
    //     
    console.log(`  : ${command}  : ${deviceId}`);
    //    
    setOpenDeviceMenu(null);
  };

  //  
  const [controlSettings, setControlSettings] = useState({
    autoReconnect: true,
    maxConnections: 10,
    sessionTimeout: 30, // 
    encryption: true,
    notifications: true,
    logAllActivities: true
  });

  //  
  const [activeCommands, setActiveCommands] = useState([
    { id: '1', device: ' Samsung Galaxy S21', command: ' ', status: 'running', progress: 65 },
    { id: '2', device: '  iPad', command: ' ', status: 'running', progress: 40 }
  ]);

  //  
  const [filter, setFilter] = useState('all');
  const [searchTerm, setSearchTerm] = useState('');

  const filteredDevices = devices.filter(device => {
    const matchesFilter = filter === 'all' || device.status === filter;
    const matchesSearch = device.name.toLowerCase().includes(searchTerm.toLowerCase()) || 
                         device.location.toLowerCase().includes(searchTerm.toLowerCase());
    return matchesFilter && matchesSearch;
  });

  //   
  useEffect(() => {
    const interval = setInterval(() => {
      setActivities(prev => {
        const newActivities = [...prev];
        newActivities.shift();
        return newActivities;
      });
    }, 5000);

    return () => clearInterval(interval);
  }, []);

  const executeCommand = (deviceId, command) => {
    alert(`   ${command}   ${deviceId}`);
  };

  const terminateSession = (deviceId) => {
    if (window.confirm(`        ${deviceId}?`)) {
      alert(`     ${deviceId}`);
    }
  };

  const COLORS = ['#3b82f6', '#10b981', '#f59e0b', '#ef4444'];

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold text-gray-800"> </h1>
        <div className="text-sm text-gray-500"> :   </div>
      </div>

      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <div className="bg-white p-6 rounded-xl shadow">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-gray-500 text-sm"> </p>
              <p className="text-3xl font-bold mt-2">42</p>
              <p className="text-green-500 text-sm mt-1">+5%  </p>
            </div>
            <div className="bg-blue-100 p-3 rounded-full">
              <FaDesktop className="text-blue-600 text-2xl" />
            </div>
          </div>
        </div>

        <div className="bg-white p-6 rounded-xl shadow">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-gray-500 text-sm"> </p>
              <p className="text-3xl font-bold mt-2">8</p>
              <p className="text-green-500 text-sm mt-1">+2%  </p>
            </div>
            <div className="bg-green-100 p-3 rounded-full">
              <FaUsers className="text-green-600 text-2xl" />
            </div>
          </div>
        </div>

        <div className="bg-white p-6 rounded-xl shadow">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-gray-500 text-sm"> </p>
              <p className="text-3xl font-bold mt-2">5</p>
              <p className="text-red-500 text-sm mt-1">-1  </p>
            </div>
            <div className="bg-yellow-100 p-3 rounded-full">
              <FaCog className="text-yellow-600 text-2xl" />
            </div>
          </div>
        </div>

        <div className="bg-white p-6 rounded-xl shadow">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-gray-500 text-sm"> </p>
              <p className="text-3xl font-bold mt-2">1.2GB</p>
              <p className="text-green-500 text-sm mt-1">+15% </p>
            </div>
            <div className="bg-purple-100 p-3 rounded-full">
              <FaPlug className="text-purple-600 text-2xl" />
            </div>
          </div>
        </div>
      </div>

      {/*   */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <div className="bg-white p-6 rounded-xl shadow">
          <h2 className="text-xl font-bold mb-4"> </h2>
          <ResponsiveContainer width="100%" height={300}>
            <BarChart data={sessionStats}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="name" />
              <YAxis />
              <Tooltip />
              <Legend />
              <Bar dataKey="sessions" fill="#3b82f6" name=" " />
              <Bar dataKey="duration" fill="#10b981" name=" ()" />
            </BarChart>
          </ResponsiveContainer>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="bg-white p-6 rounded-xl shadow">
            <h2 className="text-xl font-bold mb-4"> </h2>
            <ResponsiveContainer width="100%" height={200}>
              <PieChart>
                <Pie
                  data={deviceStats}
                  cx="50%"
                  cy="50%"
                  labelLine={false}
                  outerRadius={80}
                  fill="#8884d8"
                  dataKey="value"
                  label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                >
                  {deviceStats.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                  ))}
                </Pie>
                <Tooltip />
              </PieChart>
            </ResponsiveContainer>
          </div>

          <div className="bg-white p-6 rounded-xl shadow">
            <h2 className="text-xl font-bold mb-4"> </h2>
            <ResponsiveContainer width="100%" height={200}>
              <PieChart>
                <Pie
                  data={statusStats}
                  cx="50%"
                  cy="50%"
                  labelLine={false}
                  outerRadius={80}
                  fill="#8884d8"
                  dataKey="value"
                  label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                >
                  {statusStats.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                  ))}
                </Pie>
                <Tooltip />
              </PieChart>
            </ResponsiveContainer>
          </div>
        </div>
      </div>

      {/*   */}
      <div className="bg-white p-6 rounded-xl shadow">
        <h2 className="text-xl font-bold mb-4"> </h2>
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {activeCommands.map((command) => (
                <tr key={command.id}>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{command.device}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{command.command}</td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800">
                       
                    </span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="w-full bg-gray-200 rounded-full h-2.5">
                      <div 
                        className="bg-blue-600 h-2.5 rounded-full" 
                        style={{ width: `${command.progress}%` }}
                      ></div>
                    </div>
                    <span className="text-xs text-gray-500">{command.progress}%</span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                    <button 
                      onClick={() => executeCommand(command.device, command.command)}
                      className="text-blue-600 hover:text-blue-900 mr-3"
                    >
                      
                    </button>
                    <button 
                      className="text-red-600 hover:text-red-900"
                      onClick={() => terminateSession(command.device)}
                    >
                      
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/*   */}
      <div className="bg-white p-6 rounded-xl shadow">
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-xl font-bold"> </h2>
          <div className="flex items-center space-x-4">
            <div className="relative">
              <input
                type="text"
                placeholder="..."
                className="pl-10 pr-4 py-2 border rounded-lg"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
              <FaSearch className="absolute left-3 top-3 text-gray-400" />
            </div>
            <div className="flex space-x-2">
              <button 
                className={`px-3 py-2 rounded-lg ${filter === 'all' ? 'bg-blue-600 text-white' : 'bg-gray-200'}`}
                onClick={() => setFilter('all')}
              >
                
              </button>
              <button 
                className={`px-3 py-2 rounded-lg ${filter === 'online' ? 'bg-green-600 text-white' : 'bg-gray-200'}`}
                onClick={() => setFilter('online')}
              >
                
              </button>
              <button 
                className={`px-3 py-2 rounded-lg ${filter === 'offline' ? 'bg-red-600 text-white' : 'bg-gray-200'}`}
                onClick={() => setFilter('offline')}
              >
                 
              </button>
            </div>
          </div>
        </div>

        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider"> </th>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredDevices.map((device) => (
                <tr key={device.id}>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                      {device.type === 'mobile' ? (
                        <FaMobile className="text-blue-500 mr-2" />
                      ) : device.type === 'tablet' ? (
                        <FaTabletAlt className="text-purple-500 mr-2" />
                      ) : (
                        <FaDesktop className="text-gray-500 mr-2" />
                      )}
                      <div>
                        <div className="text-sm font-medium text-gray-900">{device.name}</div>
                        <div className="text-sm text-gray-500">{device.ip}</div>
                      </div>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                    {device.type === 'mobile' ? '' : device.type === 'tablet' ? ' ' : ''}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    {device.status === 'online' ? (
                      <span className="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800">
                        
                      </span>
                    ) : (
                      <span className="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-red-100 text-red-800">
                         
                      </span>
                    )}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="w-24 bg-gray-200 rounded-full h-2.5">
                      <div 
                        className={`h-2.5 rounded-full ${device.battery > 50 ? 'bg-green-600' : device.battery > 20 ? 'bg-yellow-500' : 'bg-red-500'}`}
                        style={{ width: `${device.battery}%` }}
                      ></div>
                    </div>
                    <span className="text-xs text-gray-500">{device.battery}%</span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{device.lastSeen}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{device.location}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                    <button 
                      onClick={() => executeCommand(device.id, ' ')}
                      className="text-blue-600 hover:text-blue-900 mr-3"
                    >
                      
                    </button>
                    <button 
                      onClick={() => executeCommand(device.id, '')}
                      className="text-green-600 hover:text-green-900 mr-3"
                    >
                      
                    </button>
                    <button 
                      onClick={() => executeCommand(device.id, ' ')}
                      className="text-purple-600 hover:text-purple-900"
                    >
                      
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/*   */}
      <div className="bg-white p-6 rounded-xl shadow">
        <h2 className="text-xl font-bold mb-4"> </h2>
        <div className="space-y-4">
          {activities.map((activity) => (
            <div key={activity.id} className="flex items-start p-3 border-b border-gray-200">
              <div className={`mr-3 mt-1 ${
                activity.status === 'success' ? 'text-green-500' : 
                activity.status === 'error' ? 'text-red-500' : 'text-yellow-500'
              }`}>
                {activity.status === 'success' ? (
                  <FaCheckCircle />
                ) : activity.status === 'error' ? (
                  <FaExclamationCircle />
                ) : (
                  <FaInfoCircle />
                )}
              </div>
              <div className="flex-1">
                <div className="flex justify-between">
                  <h3 className="font-medium">{activity.action}</h3>
                  <span className="text-sm text-gray-500">{activity.time}</span>
                </div>
                <p className="text-sm text-gray-600 mt-1">: {activity.device}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default ControlPanel;
