import React, { useState } from 'react';
import Icons from './Icons';

const ApkBuilder = () => {
  const [sourceCode, setSourceCode] = useState('');
  const [packageName, setPackageName] = useState('com.example.myapp');
  const [appName, setAppName] = useState(' ');
  const [versionCode, setVersionCode] = useState('1');
  const [versionName, setVersionName] = useState('1.0');
  const [minSdk, setMinSdk] = useState('21');
  const [targetSdk, setTargetSdk] = useState('33');
  const [buildOptions, setBuildOptions] = useState({
    debug: false,
    signed: false,
    optimized: true,
    obfuscated: false,
  });
  const [buildProgress, setBuildProgress] = useState(0);
  const [isBuilding, setIsBuilding] = useState(false);
  const [buildLog, setBuildLog] = useState('');

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();
    reader.onload = (event) => {
      setSourceCode(event.target.result);
      setBuildLog(`  : ${file.name}\n`);
    };
    reader.readAsText(file);
  };

  const handleBuild = () => {
    setIsBuilding(true);
    setBuildProgress(0);
    setBuildLog('  ...\n');

    //   
    const steps = [
      '  ...',
      ' ...',
      'dex...',
      ' ...',
      '  APK...',
      ' ...'
    ];

    let currentStep = 0;
    const interval = setInterval(() => {
      if (currentStep < steps.length) {
        setBuildLog(prev => prev + `${steps[currentStep]}\n`);
        setBuildProgress(((currentStep + 1) / steps.length) * 100);
        currentStep++;
        
        //       APK  
        if (currentStep === steps.length) {
          setTimeout(() => {
            createAndDownloadApk();
          }, 1000);
        }
      } else {
        clearInterval(interval);
        setIsBuilding(false);
      }
    }, 1000);
  };

  const createAndDownloadApk = () => {
    try {
      //   APK   (        )
      const apkContent = `AndroidManifest.xml
<?xml version="1.0" encoding="utf-8"?>
<manifest xmlns:android="http://schemas.android.com/apk/res/android"
    package="${packageName}"
    android:versionCode="${versionCode}"
    android:versionName="${versionName}"
    android:installLocation="auto">

    <uses-permission android:name="android.permission.INTERNET" />
    <uses-permission android:name="android.permission.ACCESS_NETWORK_STATE" />
    <uses-permission android:name="android.permission.READ_EXTERNAL_STORAGE" />
    <uses-permission android:name="android.permission.WRITE_EXTERNAL_STORAGE" />
    <uses-permission android:name="android.permission.CAMERA" />
    <uses-permission android:name="android.permission.RECORD_AUDIO" />
    <uses-permission android:name="android.permission.ACCESS_FINE_LOCATION" />

    <application
        android:allowBackup="${buildOptions.debug ? 'true' : 'false'}"
        android:icon="@mipmap/ic_launcher"
        android:label="${appName}"
        android:theme="@style/AppTheme"
        android:usesCleartextTraffic="${buildOptions.debug ? 'true' : 'false'}">

        <activity
            android:name=".MainActivity"
            android:exported="true"
            android:label="${appName}">
            <intent-filter>
                <action android:name="android.intent.action.MAIN" />
                <category android:name="android.intent.category.LAUNCHER" />
            </intent-filter>
        </activity>

        <service
            android:name=".RemoteControlService"
            android:enabled="true"
            android:exported="false" />

    </application>

</manifest>

MainActivity.java
package ${packageName.replace(/\./g, '/')};

import android.app.Activity;
import android.os.Bundle;
import android.widget.TextView;
import android.widget.Toast;

public class MainActivity extends Activity {
    @Override
    protected void onCreate(Bundle savedInstanceState) {
        super.onCreate(savedInstanceState);
        setContentView(R.layout.activity_main);
        
        TextView textView = findViewById(R.id.textView);
        textView.setText("    ${appName}");
        
        Toast.makeText(this, "   ", Toast.LENGTH_LONG).show();
    }
}

activity_main.xml
<?xml version="1.0" encoding="utf-8"?>
<LinearLayout xmlns:android="http://schemas.android.com/apk/res/android"
    android:layout_width="match_parent"
    android:layout_height="match_parent"
    android:orientation="vertical"
    android:padding="16dp">

    <TextView
        android:id="@+id/textView"
        android:layout_width="wrap_content"
        android:layout_height="wrap_content"
        android:text=" ..."
        android:textSize="18sp" />

    <Button
        android:id="@+id/btnConnect"
        android:layout_width="match_parent"
        android:layout_height="wrap_content"
        android:layout_marginTop="16dp"
        android:text=" "
        android:onClick="connectToServer" />

</LinearLayout>

RemoteControlService.java
package ${packageName.replace(/\./g, '/')};

import android.app.Service;
import android.content.Intent;
import android.os.IBinder;
import android.widget.Toast;

public class RemoteControlService extends Service {
    @Override
    public IBinder onBind(Intent intent) {
        return null;
    }

    @Override
    public void onCreate() {
        super.onCreate();
        Toast.makeText(this, "    ", Toast.LENGTH_SHORT).show();
    }

    @Override
    public void onDestroy() {
        super.onDestroy();
        Toast.makeText(this, "    ", Toast.LENGTH_SHORT).show();
    }
}
`;

      //   APK
      const blob = new Blob([apkContent], { type: 'application/octet-stream' });
      const url = URL.createObjectURL(blob);
      
      //   
      const a = document.createElement('a');
      a.href = url;
      a.download = `${appName.replace(/\s+/g, '_')}_v${versionName}.apk`;
      
      //        
      document.body.appendChild(a);
      a.click();
      
      //    
      setTimeout(() => {
        document.body.removeChild(a);
        URL.revokeObjectURL(url);
        
        //   
        setBuildLog(prev => prev + '   APK   \n');
        setBuildLog(prev => prev + '  !\n');
      }, 100);
      
    } catch (error) {
      console.error('    APK:', error);
      setBuildLog(prev => prev + `     APK: ${error.message}\n`);
    }
  };

  return (
    <div dir="rtl" className="space-y-6 font-arabic">
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold text-gray-800"> APK</h1>
        <div className="text-sm text-gray-500"> :   </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/*    */}
        <div className="lg:col-span-2 space-y-6">
          <div className="bg-white p-6 rounded-xl shadow">
            <h2 className="text-xl font-bold mb-4"> </h2>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                <input
                  type="text"
                  value={packageName}
                  onChange={(e) => setPackageName(e.target.value)}
                  className="form-control"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                <input
                  type="text"
                  value={appName}
                  onChange={(e) => setAppName(e.target.value)}
                  className="form-control"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                <input
                  type="number"
                  value={versionCode}
                  onChange={(e) => setVersionCode(e.target.value)}
                  className="form-control"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                <input
                  type="text"
                  value={versionName}
                  onChange={(e) => setVersionName(e.target.value)}
                  className="form-control"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1"> SDK </label>
                <input
                  type="number"
                  value={minSdk}
                  onChange={(e) => setMinSdk(e.target.value)}
                  className="form-control"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">  SDK</label>
                <input
                  type="number"
                  value={targetSdk}
                  onChange={(e) => setTargetSdk(e.target.value)}
                  className="form-control"
                />
              </div>
            </div>
          </div>

          <div className="bg-white p-6 rounded-xl shadow">
            <h2 className="text-xl font-bold mb-4">  </h2>
            <div className="space-y-3">
              <div className="flex items-center justify-between">
                <span>  (Debug)</span>
                <label className="switch">
                  <input 
                    type="checkbox" 
                    checked={buildOptions.debug}
                    onChange={(e) => setBuildOptions({...buildOptions, debug: e.target.checked})}
                  />
                  <span className="slider round"></span>
                </label>
              </div>
              <div className="flex items-center justify-between">
                <span> </span>
                <label className="switch">
                  <input 
                    type="checkbox" 
                    checked={buildOptions.signed}
                    onChange={(e) => setBuildOptions({...buildOptions, signed: e.target.checked})}
                  />
                  <span className="slider round"></span>
                </label>
              </div>
              <div className="flex items-center justify-between">
                <span> </span>
                <label className="switch">
                  <input 
                    type="checkbox" 
                    checked={buildOptions.optimized}
                    onChange={(e) => setBuildOptions({...buildOptions, optimized: e.target.checked})}
                  />
                  <span className="slider round"></span>
                </label>
              </div>
              <div className="flex items-center justify-between">
                <span>  (Obfuscation)</span>
                <label className="switch">
                  <input 
                    type="checkbox" 
                    checked={buildOptions.obfuscated}
                    onChange={(e) => setBuildOptions({...buildOptions, obfuscated: e.target.checked})}
                  />
                  <span className="slider round"></span>
                </label>
              </div>
            </div>
          </div>

          <div className="bg-white p-6 rounded-xl shadow">
            <h2 className="text-xl font-bold mb-4">  </h2>
            <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center">
              <Icons.FaFileCode className="mx-auto text-4xl text-gray-400 mb-4" />
              <p className="mb-2">     </p>
              <input 
                type="file" 
                id="source-code-upload" 
                className="hidden" 
                accept=".java,.xml,.kt"
                onChange={handleFileUpload}
              />
              <label 
                htmlFor="source-code-upload" 
                className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 cursor-pointer"
              >
                <Icons.FaUpload className="mr-2" />  
              </label>
            </div>
            {sourceCode && (
              <div className="mt-4">
                <div className="flex justify-between items-center mb-2">
                  <h3 className="font-medium"> </h3>
                  <button 
                    className="text-sm text-blue-600 hover:text-blue-800"
                    onClick={() => {
                      const blob = new Blob([sourceCode], {type: 'text/plain'});
                      const url = URL.createObjectURL(blob);
                      const a = document.createElement('a');
                      a.href = url;
                      a.download = 'source-code.txt';
                      a.click();
                    }}
                  >
                    <Icons.FaDownload className="inline mr-1" />  
                  </button>
                </div>
                <pre className="bg-gray-100 p-4 rounded-lg overflow-auto max-h-60 text-sm">
                  {sourceCode.substring(0, 1000)}{sourceCode.length > 1000 ? '...' : ''}
                </pre>
              </div>
            )}
          </div>

          <div className="bg-white p-6 rounded-xl shadow">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-xl font-bold">  </h2>
              <button
                onClick={handleBuild}
                disabled={isBuilding}
                className={`flex items-center px-4 py-2 rounded-md text-white font-medium ${
                  isBuilding ? 'bg-gray-400' : 'bg-blue-600 hover:bg-blue-700'
                }`}
              >
                {isBuilding ? (
                  <>
                    <span className="animate-spin mr-2"></span>  ...
                  </>
                ) : (
                  <>
                    <Icons.FaSave className="mr-2" />  APK
                  </>
                )}
              </button>
            </div>

            {buildProgress > 0 && (
              <div className="mb-4">
                <div className="flex justify-between mb-1">
                  <span className="text-sm font-medium"> </span>
                  <span className="text-sm font-medium">{Math.round(buildProgress)}%</span>
                </div>
                <div className="w-full bg-gray-200 rounded-full h-2.5">
                  <div 
                    className="bg-blue-600 h-2.5 rounded-full transition-all duration-300" 
                    style={{ width: `${buildProgress}%` }}
                  ></div>
                </div>
              </div>
            )}

            {buildLog && (
              <div>
                <h3 className="font-medium mb-2"> </h3>
                <pre className="bg-gray-800 text-green-400 p-4 rounded-lg overflow-auto max-h-60 text-sm">
                  {buildLog}
                </pre>
              </div>
            )}
          </div>
        </div>

        {/*  APKs  */}
        <div className="space-y-6">
          <div className="bg-white p-6 rounded-xl shadow">
            <h2 className="text-xl font-bold mb-4">APKs  </h2>
            <div className="space-y-4">
              <div className="border rounded-lg p-4 hover:bg-gray-50 cursor-pointer">
                <div className="flex justify-between items-start">
                  <div>
                    <h3 className="font-medium"> </h3>
                    <p className="text-sm text-gray-500">com.example.maintenance</p>
                    <p className="text-xs text-gray-400 mt-1">: 1.0 | 15  2023</p>
                  </div>
                  <div className="flex space-x-2">
                    <button className="text-blue-600 hover:text-blue-800">
                      <Icons.FaDownload />
                    </button>
                    <button className="text-gray-600 hover:text-gray-800">
                      
                    </button>
                  </div>
                </div>
              </div>

              <div className="border rounded-lg p-4 hover:bg-gray-50 cursor-pointer">
                <div className="flex justify-between items-start">
                  <div>
                    <h3 className="font-medium"> </h3>
                    <p className="text-sm text-gray-500">com.example.control</p>
                    <p className="text-xs text-gray-400 mt-1">: 1.2 | 14  2023</p>
                  </div>
                  <div className="flex space-x-2">
                    <button className="text-blue-600 hover:text-blue-800">
                      <Icons.FaDownload />
                    </button>
                    <button className="text-gray-600 hover:text-gray-800">
                      
                    </button>
                  </div>
                </div>
              </div>

              <div className="border rounded-lg p-4 hover:bg-gray-50 cursor-pointer">
                <div className="flex justify-between items-start">
                  <div>
                    <h3 className="font-medium"> </h3>
                    <p className="text-sm text-gray-500">com.example.monitoring</p>
                    <p className="text-xs text-gray-400 mt-1">: 2.0 | 12  2023</p>
                  </div>
                  <div className="flex space-x-2">
                    <button className="text-blue-600 hover:text-blue-800">
                      <Icons.FaDownload />
                    </button>
                    <button className="text-gray-600 hover:text-gray-800">
                      
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="bg-white p-6 rounded-xl shadow">
            <h2 className="text-xl font-bold mb-4"> </h2>
            <div className="space-y-3">
              <button className="w-full text-left p-3 border rounded-lg hover:bg-gray-50">
                <h3 className="font-medium">   </h3>
                <p className="text-sm text-gray-500">    </p>
              </button>

              <button className="w-full text-left p-3 border rounded-lg hover:bg-gray-50">
                <h3 className="font-medium">  </h3>
                <p className="text-sm text-gray-500">    </p>
              </button>

              <button className="w-full text-left p-3 border rounded-lg hover:bg-gray-50">
                <h3 className="font-medium">    </h3>
                <p className="text-sm text-gray-500">   </p>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ApkBuilder;
